import { Injectable } from '@angular/core';
import { Cart } from '../models/cart.models';
import { Item } from '../models/item.model';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor() { }

  public item: Cart[] = [];
  public quantity: number;
  public add(item: Item) {
    for (let i = 0; i < this.item.length; i++) {
      if (this.item[i].item.id == item.id) {
        this.item[i].quantity++;
        return;
      }
    }
    this.item.push(new Cart(item, 1));
  }

  public remove(item: Item) {
    for (let i = 0; i < this.item.length; i++) {
      if (this.item[i].item.id == item.id) {
        if (this.item[i].quantity >= 1) {
          this.item[i].quantity--;
        }
        
        // this.item.push(new Cart(item, this.item[i].quantity--));

        return;
      }
    }

  }

  public removeAllOfAnItem(item: Item) {
    for (let i = 0; i < this.item.length; i++) {
      if (this.item[i].item.id == item.id) {
        if (this.item = this.item.splice(i, 1))
          return;
      }
    }
  }


  public clear() {
    this.item.length = 0;
  }

  public get grandTotal(): number {
    let total = 0;
    for (let item of this.item) {
      total += item.totalPrice;
    }
    return total;
  }

  public get Cart(): Cart[] {
    return this.item;
  }

  public getQuantityByItem(item: Item): number {
    let r = this.item.find((p) => p.item.id == item.id);
    return r == undefined ? 0 : r.quantity;
  }

}
